import request from '@/utils/crmRequest'

/********* 小助手管理 ************/

// 获取小助手列表
export function getHelperList(params) {
  return request({
    url: '/corp/wechat/helper',
    method: 'get',
    params
  })
}

// 获取员工列表
export function getAdminList(params) {
  return request({
    url: '/corp/admin',
    method: 'get',
    params
  })
}

// 获取列表
export function getHelper(params) {
  return request({
    url: '/corp/helper',
    method: 'get',
    params
  })
}

// 创建小助手
export function createHelper(data) {
  return request({
    url: '/corp/helper',
    method: 'post',
    data
  })
}

// 匹配运营人员
export function matchingHelper(id, data) {
  return request({
    url: '/corp/helper/' + id,
    method: 'put',
    data
  })
}

// 更改运营人员
export function updateHelper(id, data) {
  return request({
    url: '/corp/helper/change/' + id,
    method: 'put',
    data
  })
}

// 删除小助手
export function deleteHelper(id) {
  return request({
    url: '/corp/helper/' + id,
    method: 'delete'
  })
}

/********* 小助手管理 ************/

