<template>
  <div class="follow-operation">
    <!--  搜索  -->
    <el-form
      ref="followSearch"
      label-position="left"
      :model="followSearch"
      inline
    >
      <el-form-item label="跟进类型" class="search-item">
        <el-select
          v-model="followSearch.type"
          clearable
          placeholder="请选择跟进类型"
        >
          <el-option
            v-for="item in typeList"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          />
        </el-select>
      </el-form-item>
      <el-form-item label="跟进动作名称" class="search-item">
        <el-input
          v-model="followSearch.name"
          clearable
          placeholder="请输入跟进动作名称"
        />
      </el-form-item>
      <el-form-item label="状态" class="search-item">
        <el-select
          v-model="followSearch.status"
          clearable
          placeholder="请选择状态"
        >
          <el-option
            v-for="item in statusList"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          />
        </el-select>
      </el-form-item>
      <el-form-item label="创建人" class="search-item">
        <el-input
          v-model="followSearch.founder"
          clearable
          placeholder="请输入创建人"
        />
      </el-form-item>
      <el-form-item style="float: right">
        <el-button type="primary" class="select-btn" @click="onSubmit"
          >查询</el-button
        >
        <el-button type="primary" class="select-btn" @click="resetForm"
          >重置</el-button
        >
      </el-form-item>
    </el-form>
    <!--  列表  -->
    <div class="list">
      <div class="title">
        <el-button type="primary" @click="setFollowOperation(1)"
          >新增跟进动作</el-button
        >
      </div>
      <el-table
        :data="list"
        style="width: 100%"
        border
        :empty-text="hasListInfo"
      >
        <el-table-column
          fixed
          prop="num"
          label="序号"
          width="100"
          align="center"
        />
        <el-table-column prop="name" label="跟进动作名称" align="center" />
        <el-table-column prop="type" label="跟进类型" align="center" />
        <el-table-column prop="status" label="状态" align="center">
          <template slot-scope="{ row }">
            <el-switch v-model="row.status" @change="setStatus(row.id)" />
          </template>
        </el-table-column>
        <el-table-column prop="founder" label="创建人" align="center" />
        <el-table-column prop="created_at" label="创建时间" align="center" />
        <el-table-column fixed="right" label="操作" width="200" align="center">
          <template slot-scope="scope">
            <el-button
              type="text"
              size="small"
              @click="setFollowOperation(2, scope.row)"
              >编辑</el-button
            >
            <el-button
              type="text"
              size="small"
              @click="
                rowInfo = scope.row
                delShow = true
              "
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <div class="pagination">
        <el-pagination
          :current-page="currentPage"
          :page-sizes="[10, 20, 50]"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="count"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
        />
      </div>
    </div>
    <el-dialog
      title="删除提示"
      :visible.sync="delShow"
      width="400px"
      center
      top="30vh"
    >
      <p class="center">
        删除后，该跟进动作将不存在，
        <br />请确定是否删除？
      </p>
      <span slot="footer" class="dialog-footer">
        <el-button @click="delConfirm">确 定</el-button>
        <el-button type="primary" @click="delShow = false">取 消</el-button>
      </span>
    </el-dialog>
    <el-dialog
      :title="followTitle"
      :visible.sync="followShow"
      width="400px"
      center
      top="30vh"
    >
      <el-form
        :model="setFollowForm"
        ref="setFollowForm"
        label-width="110px"
        class="demo-ruleForm"
      >
        <el-form-item
          label="跟进类型"
          prop="type"
          :rules="
            followShow
              ? [{ required: true, message: '请选择', target: 'blur' }]
              : {}
          "
        >
          <el-select
            v-model="setFollowForm.type"
            filterable
            allow-create
            clearable
            placeholder="请选择跟进类型"
          >
            <el-option
              v-for="item in typeList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            />
          </el-select>
        </el-form-item>
        <el-form-item
          label="跟进动作名称"
          prop="name"
          :rules="
            followShow
              ? [
                  {
                    required: true,
                    message: '请输入跟进动作名称',
                    target: 'blur'
                  }
                ]
              : {}
          "
        >
          <el-input
            v-model="setFollowForm.name"
            clearable
            placeholder="请输入跟进动作名称"
          />
        </el-form-item>
        <el-form-item label="描述" prop="description">
          <el-input
            type="texarea"
            v-model="setFollowForm.description"
            clearable
            placeholder="请输入相关描述"
          />
        </el-form-item>
        <div style="text-align: center">
          <el-button type="primary" @click="inputFormSubmit('setFollowForm')">
            {{ btnText }}
          </el-button>
        </div>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import {
  getFollowList,
  createFollow,
  updateFollow,
  deleteFollow,
  setStatus
} from '@/api/assistantManage'
export default {
  data() {
    return {
      isOpen: false,
      list: [],
      currentPage: 1,
      delShow: false,
      followTitle: '',
      btnText: '',
      followShow: false,
      pageSize: 10,
      count: 0,
      rowInfo: '',
      hasListInfo: '暂无数据',
      singleList: [],
      createNameList: [],
      followSearch: {
        type: '',
        name: '',
        status: '',
        founder: ''
      },
      typeList: [
        { value: 'first', label: '首次跟进' },
        { value: 'take_care', label: '回访' },
        { value: 'visit', label: '日常关心' },
        { value: 'announcement', label: '活动通知' }
      ],
      statusList: [
        { value: 'online', label: '已启用' },
        { value: 'offline', label: '已禁用' }
      ],
      setFollowForm: {
        type: '',
        name: '',
        description: ''
      }
    }
  },
  computed: {
    getFollowTypeCh() {
      return function(type) {
        if (type === 'first') return '首次跟进'
        if (type === 'take_care') return '回访'
        if (type === 'visit') return '日常关心'
        if (type === 'announcement') return '活动通知'
      }
    }
  },
  watch: {
    followShow(value) {
      if (!value) {
        this.setFollowForm = {
          type: '',
          name: '',
          description: ''
        }
      }
    }
  },
  created() {
    this.getFollowList()
  },
  methods: {
    onSubmit() {
      this.currentPage = 1
      this.getFollowList(this.followSearch)
    },
    handleSizeChange(val) {
      this.pageSize = val
      this.getFollowList(this.followSearch)
    },
    handleCurrentChange(val) {
      this.currentPage = val
      this.getFollowList(this.followSearch)
    },
    resetForm() {
      this.followSearch = {
        type: '',
        name: '',
        status: '',
        founder: ''
      }
      this.getFollowList()
    },
    async delConfirm() {
      const { code, message } = await deleteFollow(this.rowInfo.id)
      if (code === 10000) {
        this.$message.success(message)
        this.getFollowList(this.followSearch)
        this.delShow = false
      }
    },
    setFollowOperation(num, row) {
      if (num === 1) {
        this.followTitle = '新增跟进动作'
        this.btnText = '确认新增'
      } else if (num === 2) {
        this.followTitle = '编辑跟进动作'
        this.btnText = '确认编辑'
        this.setFollowForm = {
          type: row.typeEn,
          name: row.name,
          description: row.description === '/' ? '' : row.description
        }
      }
      this.rowInfo = row || {}
      this.rowInfo.num = num
      this.followShow = true
    },
    inputFormSubmit(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          if (this.rowInfo.num === 1) this.createFollow()
          if (this.rowInfo.num === 2) this.updateFollow()
        } else {
          return false
        }
      })
    },
    async getFollowList(search) {
      let params = {
        pageSize: this.pageSize,
        page: this.currentPage
      }
      if (search) {
        if (search.name) params.name = search.name
        if (search.type) params.type = search.type
        if (search.status) params.status = search.status
        if (search.founder) params.creator_name = search.founder
        this.hasListInfo = '暂无数据，请重新输入关键词'
      } else {
        this.hasListInfo = '暂无数据'
      }
      const { data, code } = await getFollowList(params)
      if (code === 10000) {
        this.count = data.count
        this.list = data.list.map((item, index) => {
          return {
            num: (this.currentPage - 1) * this.pageSize + index + 1,
            id: item._id,
            name: item.name,
            type: this.getFollowTypeCh(item.type),
            typeEn: item.type,
            status: item.status === 'online',
            founder: item.creator_name,
            created_at: item.created_at
              ? this.formatTime(item.created_at)
              : '-',
            description: item.describe || '/'
          }
        })
      }
    },
    async createFollow() {
      const data = {
        type: this.setFollowForm.type,
        name: this.setFollowForm.name,
        describe: this.setFollowForm.description || undefined
      }
      const { code, message } = await createFollow(data)
      if (code === 10000) {
        this.$message.success(message)
        this.getFollowList(this.followSearch)
        this.followShow = false
      }
    },
    async updateFollow() {
      const data = {
        type: this.setFollowForm.type,
        name: this.setFollowForm.name,
        describe: this.setFollowForm.description || undefined
      }
      const { code, message } = await updateFollow(this.rowInfo.id, data)
      if (code === 10000) {
        this.$message.success(message)
        this.getFollowList(this.followSearch)
        this.followShow = false
      }
    },
    async setStatus(id) {
      const { code, message } = await setStatus(id)
      if (code === 10000) {
        this.$message.success(message)
        this.getFollowList(this.followSearch)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.follow-operation {
  display: flex;
  flex-wrap: wrap;
  font-size: 14px;
  margin-bottom: 20px;
  /deep/form {
    width: 100%;
    .el-date-editor {
      padding: 0 10px;
      width: 220px;
      height: 32px;
      .el-range-input {
        width: 85px;
        height: 30px;
      }
    }
  }
  .search-item {
    color: #17233d;
    &:not(:nth-child(4)) {
      margin-right: 50px;
    }
    /deep/input {
      width: 220px;
      height: 32px;
      line-height: 32px;
    }
    /deep/span {
      top: -3px;
    }
    /deep/.el-input__suffix {
      top: 0;
    }
  }
  .select-btn {
    padding: 0 20px;
    width: 68px;
    height: 32px;
  }
  .arrow {
    float: right;
  }
  .list {
    margin: 20px 0 0;
    width: 100%;
    .title {
      line-height: 32px;
      text-align: right;
      /deep/button {
        margin-bottom: 10px;
        padding: 6px 20px;
        height: 32px;
      }
    }
    /deep/.el-dialog__wrapper {
      button {
        padding: 6px 20px;
        height: 32px;
      }
      .el-dialog__headerbtn {
        top: 10px;
        right: 5px;
      }
    }
    .pagination {
      margin-top: 50px;
      text-align: center;
    }
  }
  /deep/.cell {
    overflow: hidden;
    -ms-text-overflow: ellipsis;
    text-overflow: ellipsis;
    white-space: nowrap;
    *:not(button) {
      display: inline-block;
      width: 100%;
    }
  }
  .center {
    line-height: 20px;
    text-align: center;
  }
  .button-wrap {
    button {
      height: 32px;
      line-height: 0;
    }
  }
  .danger-tip {
    margin-top: 10px;
    line-height: 16px;
    color: #f00;
  }
  .el-dialog__wrapper {
    /deep/.el-input__inner {
      width: 220px;
    }
  }
  .demo-ruleForm {
    /deep/.el-input {
      width: 220px;
    }
  }
}
</style>
